<template>
  <div id="updateGesture">
    <header-title header-title="重置手势密码"></header-title>
    <div id="alert">
      <div><img src="../../static/img/updateGesture/Combined Shape@2x.png"></div>
      <div>绘制解锁图案</div>
    </div>
    <div id="patternLock">
    </div>
    <br/>
    <br/>
    <br/>
    <mt-button @click="reset" size="small">重置</mt-button>
  </div>
</template>

<script>
  import PatternLock from '../../static/js/patternLock.js'
  import HeaderTitle from "./HeaderTitle"

    export default {
      name: "updateGesture",
      data(){
        return{
          lock : {}
        }
      },
      mounted: function () {
        var self = this;
        self.lock = new PatternLock('#patternLock',{
          onDraw:function(pattern){
            //do something with pattern
            console.log(pattern);
            if(pattern == '1235789'){
              self.$toast('输入错误！');
              // self.$messagebox('输入错误！');
              self.lock.error();
              setTimeout(function(){
                self.lock.reset();
              },2000)
            }
          }
        });
      },
      methods: {
        reset: function () {
          this.lock.reset();
        }
      },
      components: {
        HeaderTitle
      }
    }
</script>

<style scoped>
  body{
    background-color: #fff;
  }
  #updateGesture > .ssTitle {
    background: #f1f1f1;
  }
  #updateGesture > .ssTitle > span > h4{
    font-size:17px;
    font-family:PingFangSC-Regular;
    color:rgba(85,85,85,1);
  }
  #alert{
    padding: 61px 0 24px;
  }
  #alert > div > img{
    width: 35px;
    height: 33px;
  }
  #alert > div {
    font-size:15px;
    font-family:PingFangSC-Regular;
    color:rgba(153,153,153,1);
    padding-bottom: 10px;
  }
  #patternLock{
    margin: 0 auto;
    /*background-color: #fff;    */
  }
  .patt-wrap > li {
    width: 76px;
    height: 76px;
  }
  .patt-wrap > li > .patt-wrap {
    width: 30px;
    height: 30px;
  }
</style>



